body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background-color: #f5f6fa !important;
}

:lang(jp) {
  font-family: "Sawarabi Gothic";
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

li {
  list-style: none;
}

p {
  margin: 0;
}

.p-white {
  background-color: #ffffff;
}
a:hover{
  text-decoration: none !important;
}

.header-main{
  width: 100%;
  display: block;
  background-color: #ffffff;
  border-bottom: 1px solid #e0e0e8;
  padding-top: 16px;
  position: fixed;
  top: 0px;
  z-index: 1;
}

.hm-content-area{
  display: flex;
  width: 1400px;
  margin: 0 auto;
}

.hm-logo{
  width: 50px;
  height: 50px;
  display: block;
  margin: 0 auto;
  border-radius: 25px;
  -webkit-transition: all 0.4s;
  transition: all 0.4s;
  -webkit-animation: Header-logo-spin infinite 5s linear;
          animation: Header-logo-spin infinite 5s linear;
}

.hm-logo:hover{
  opacity: 0.7;
}

.hm-nav-content-area{
  line-height: 50px;
}

.hm-nav-bar{
  margin: 0;
  display: flex;
}

.hm-nav-list{
  margin-right: 40px;
  font-size: 23px;
  font-weight: bold;
}

@-webkit-keyframes Header-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes Header-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}
.edc-Component{
  margin-top: 40px;
  border: 1px solid #e0e0e8;
  padding: 10px;
}

.eeda{
  height: 40px;
  line-height: 40px;
}

.eeda-text{
  display: inline-block;
}

.eeda-title{
  width: 230px;
  font-size: 23px;
  font-weight: bold;
  margin-bottom: 7px;
}

.eeda-content{
  padding-left: 10px;
}

#EDC, #CurrentTime{
  padding-top: 3px;
  position: absolute;
}
.main{
  position: relative;
  top: 80px;
  z-index: 0;
}

.main-body{
  width: 1400px;
  margin: 0 auto;
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

